import {login,queryCard,queryCouponCanUsed,vipQueryCardType} from '@/common/api.js'
import cardConfig from '@/common/card-config.js'
import couponConfig from '@/common/coupon-config.js'
import aes from '@/utils/aes.js'
export default {
	onPullDownRefresh() {
		//开始查卡和券
		this.COMMONFUNCTION.getCode().then((res)=>this.query(res)).catch((reason)=>console.log(reason))
		uni.showNavigationBarLoading();
		
	},
	onLoad() {
		this.color=this.BASECOLOR;
		this.ossDownloadUrl = this.HTTPCONFIG.ossDownloadUrl;
		uni.showNavigationBarLoading();		
	},
	onShow() {
		this.COMMONFUNCTION.getCode().then((res)=>this.query(res)).catch((reason)=>console.log(reason))
		uni.showNavigationBarLoading();
	},
	data() {
		return {
			appData:{},
			//是否授权
			title:"登录/注册",
			hasRegist:true,
			color:'',
			userInfo:'',
			cardList:[],
			ossDownloadUrl:'',
			couponList:[
			],
			
		}
	},
	methods: {
		toMyCards(){
			 this.COMMONFUNCTION.toPage('/pages/mycards/mycards')
		},
		toMycard(item){
			this.COMMONFUNCTION.toPage('/pages/mycards/card?grpId='+item.grpCode+'&cardId='+item.cardType)
		},
		//登录
		query(code){
			login(code,'',true).then(res=>{
				queryCard(1,'','',1,3,true).then(res=>{
					this.cardList=res.cardOwnList;
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
				}).catch(res=>{
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();						
					
				})
				//查券
				queryCouponCanUsed(1,10,'','','','',true).then((res)=>{
					this.couponList=res.data;
				}).catch(e=>{
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
				})
			}).catch(e=>{
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
			})
		},
		scanCode(){
			uni.scanCode({
				scanType:'qrCode',
				success:(res)=> {
					var url=res.result;
					console.log(url)
					if(url.indexOf('getCards')!=-1){
						this.COMMONFUNCTION.saveStorage('qrCodeUrl',url).then(res=>{
							this.COMMONFUNCTION.toPage('/pages/getCards/getCards?from='+1)
						})
						return
					}
					if(url.indexOf('getCoupons')!=-1){
						this.COMMONFUNCTION.saveStorage('qrCodeUrl',url).then(res=>{
							this.COMMONFUNCTION.toPage('/pages/getCoupons/getCoupons?from='+1)
						})
						return
					}
					uni.showToast({
						icon:'none',
						title:'二维码内容无效!'
					})
				}
				
			})
		},
		toCoupons(){
			this.COMMONFUNCTION.toPage("/pages/mycoupon/mycoupons");
			
		},				
		toCoupon(item){
			this.COMMONFUNCTION.toPage('/pages/mycoupon/mycoupon?couponCode='+item.couponCode+'&couponId='+item.couponId)
		},
		userAgreement(){
			this.COMMONFUNCTION.toPage('/pages/about-content/userAgreement')
		},
		privAgreement(){
			this.COMMONFUNCTION.toPage('/pages/about-content/privacyAgreement')
			
		}

	}
}
